<template>
  <PopupBase v-bind:show="showPopupBase" class="r-modal" @onClose="onClose">
    <div v-if="showMask" class="r-modal-mask" @click="clickMask"></div>
    <div
      class="r-modal-wrapper"
      :class="[
        `r-modal-wrapper-position-${position}`,
        { ...wrapperCustomClass }
      ]"
    >
      <transition
        :enter-active-class="`animate__${animateIn}`"
        :leave-active-class="`animate__${animateOut}`"
      >
        <div
          :class="[
            'r-modal-content',
            'animate__animated',
            ...contentCustomClass
          ]"
          v-if="showModalContent"
        >
          <slot>
            <div v-if="dangerInnerHtml" v-html="content"></div>
            <div v-else>{{ content }}</div>
          </slot>
        </div>
      </transition>
    </div>
  </PopupBase>
</template>
<script lang="ts">
import { defineComponent, ref, nextTick, watch } from 'vue'
import PopupBase from '../popup-base'
export default defineComponent({
  name: 'rModal',
  components: { PopupBase },
  props: {
    /**
     * v-model:show
     */
    show: {
      type: Boolean,
      default: false
    },
    /**
     * 
     */
    showMask: {
      type: Boolean,
      default: true
    },
    /**
     * 
     */
    closeOnClickMask: {
      type: Boolean,
      default: false
    },
    /**
     * animate.css
     */
    animateIn: {
      type: String,
      default: 'fadeIn'
    },
    /**
     *  animate.css
     */
    animateOut: {
      type: String,
      default: 'fadeOut'
    },
    /**
     *  bodyoverflow: hidden
     */
    bodyOverflow: {
      type: Boolean,
      default: true
    },
    /**
     *  class r-modal-content
     */
    contentCustomClass: {
      type: Array,
      default: (): string[] => []
    },
    /**
     *  classr-modal-wrapper
     */
    wrapperCustomClass: {
      type: Array,
      default: (): string[] => []
    },
    /**
     *  
     */
    content: {
      type: String,
      default: ''
    },
    /**
     *  html
     */
    dangerInnerHtml: {
      type: Boolean,
      default: false
    },
    /**
     *  top | center | bottom
     */
    position: {
      type: String,
      default: 'center'
    }
  },
  emits: {
    /**
     * 
     */
    onClose: null,
    /**
     * 
     */
    'update:show': (isShow: boolean) => isShow,
    /**
     * 
     */
    clickMask: null
  },
  setup (props, ctx) {
    const showModalContent = ref(false)
    const showPopupBase = ref(false)
    const modalContentRef = ref(null)

    watch(
      () => props.show,
      async (show, prevShow) => {
        if (show !== prevShow) {
          if (show === true) {
            showPopupBase.value = true
            await nextTick()
            showModalContent.value = true
          } else {
            showModalContent.value = false
            await nextTick()
            showPopupBase.value = false
          }

          if (props.bodyOverflow) {
            show
              ? document.body.classList.add('r-modal-parent--hidden')
              : document.body.classList.remove('r-modal-parent--hidden')
          }
        }
      }
    )

    // 
    const clickMask = () => {
      ctx.emit('clickMask')
      if (props.closeOnClickMask) {
        ctx.emit('update:show', false)
      }
    }

    const onClose = () => {
      ctx.emit('onClose')
    }

    return {
      showPopupBase,
      showModalContent,
      modalContentRef,
      clickMask,
      onClose
    }
  }
})
</script>
